@import "variables";

label,
input,
button,
select,
textarea {
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
}

input,
button,
select,
textarea {
  font-family: "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.uneditable-input,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"] {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-appearance: none !important;
  color: #282323;
  outline: 0;
  height: 16px;
  padding: 6px 11px !important;
  line-height: 15px;
  font-size: 13px;
  font-weight: normal;
  vertical-align: top;
  background-color: $color-white;
  min-height: 37px;
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  border: 1px solid #e5e9ec;
  transition: background 0.2s linear 0s, box-shadow 0.2s linear 0s;
}

input {
  border: 1px solid #cecece;
  &.no-boarder {
    border: none;
  }
  &.dark {
    background-color: #e5e9ec;
    border: none;
  }
  &.error {
    border: 1px solid #f35958;
  }
}

textarea {
  height: auto;
  padding-left: 12px !important;
  padding-top: 10px !important;
}

.uneditable-input.focus,
textarea:focus,
select:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.m-uneditable-input:focus {
  border-color: $input-active-bordrer-color;
  background-color: $input-active-background-color;
  outline: 0 !important;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

input[type="radio"],
input[type="checkbox"] {
  margin: 4px 0 0;
  margin-top: 1px;
  line-height: normal;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

select,
input[type="file"] {
  height: 34px !important;
  line-height: 30px;
}

select {
  width: 220px;
  background-color: $color-white;
  background-image: none !important;
  border: 1px solid #e5e5e5;
  outline: none;
  min-height: 35px;
  &.xsmall {
    width: 64px !important;
  }
  &.small {
    width: 134px !important;
  }
  &.medium {
    width: 220px !important;
  }
  &.large {
    width: 334px !important;
  }
}

select[multiple],
select[size] {
  height: auto !important;
}

select:focus,
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
  outline: thin dotted #333333;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
}

input[class*="span"] {
  float: none;
  height: 34px !important;
  margin-left: 0;
}

select[class*="span"],
textarea[class*="span"] {
  float: none;
  margin-left: 0;
}

.input-lg {
  border-radius: 3px !important;
  font-size: 18px !important;
  height: 45px !important;
  line-height: 1.33 !important;
}

.input-sm {
  font-size: 13px !important;
  min-height: 29px !important;
}

.input-prepend,
.input-append {
  border: 0;
  display: inline-block;
}

.input-append .add-on,
.input-prepend .add-on {
  display: inline-block;
  width: auto;
  height: 24px;
  min-width: 16px;
  padding: 4px 5px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  text-align: center;
  text-shadow: 0 1px 0 $color-white;
  background-color: #e5e5e5;
  border: 1px solid #e5e5e5;
}

.input-append .add-on > .halflings-icon,
.input-prepend .add-on > i,
.input-prepend .add-on > .fa {
  margin-top: 5px;
  margin-left: 3px;
}

.input-append .add-on > .halflings-icon
.input-append .add-on > i,
.input-append .add-on > .fa {
  margin-top: 5px;
  margin-left: 0px;
}

.input-append,
.input-append :focus {
  border-right: 0 !important;
}

.input-append input[class*="span"],
.input-prepend input[class*="span"],
.input-append input, .input-prepend input {
  display: inline-block !important;
  width: 100%;
  float: left;
}

.xsmall {
  width: 50px !important;
}

.small {
  width: 120px !important;
}

.medium {
  width: 206px !important;
}

.large {
  width: 320px !important;
}

.huge {
  width: 480px !important;
  font-size: 24px !important;
  height: 36px !important;
  line-height: 36px !important;
  padding: 22px 8px !important;
}

.radio, .checkbox {
  margin-bottom: 0;
  margin-top: 0;
  padding-left: 0px;
}

.radio {
  label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    padding-left: 25px;
    margin-right: 15px;
    font-size: 13px;
    margin-bottom: 6px;
    color: #777a80;
    &:before {
      content: "";
      display: inline-block;
      width: 17px;
      height: 17px;
      margin-right: 10px;
      position: absolute;
      left: 0;
      bottom: 2.5px;
      background-color: $color-white;
      border: 1px solid #c2c6cb;
      border-radius: 99px;
    }
    &:after {
      font-size: 13px;
      content: "";
      text-align: center;
      display: inline-block;
      width: 17px;
      height: 17px;
      position: absolute;
      -o-transform: scale(0);
      -ms-transform: scale(0);
      -moz-transform: scale(0);
      -webkit-transform: scale(0);
      transform: scale(0);
      border-radius: 99px;
      left: 0px;
      top: 1px;
      background-color: #6f7b8a;
      border: 1px solid #6f7b8a;
    }
  }
  input[type=radio] {
    display: none;
  }
  input[type=radio][disabled] {
    & + label {
      opacity: 0.65;
    }
  }
  &.radio-success {
    label {
      &:after {
        background-color: $color-success;
        border: 1px solid $color-success;
      }
    }
    input[type=radio]:checked {
      & + label {
        &:before {
          border: 1px solid $color-success;
        }
      }
    }
  }
  &.radio-primary {
    label {
      &:after {
        background-color: $color-primary;
        border: 1px solid $color-primary;
      }
    }
    input[type=radio]:checked {
      & + label {
        &:before {
          border: 1px solid $color-primary;
        }
      }
    }
  }
  &.radio-info {
    label {
      &:after {
        background-color: $color-info;
        border: 1px solid $color-info;
      }
      input[type=radio]:checked {
        & + :before {
          border: 1px solid $color-info;
        }
      }
    }
  }
  &.radio-warning {
    label {
      &:after {
        background-color: $color-warning;
        border: 1px solid $color-warning;
      }
    }
    input[type=radio]:checked {
      & + label {
        &:before {
          border: 1px solid $color-warning;
        }
      }
    }
  }
  &.radio-danger {
    label {
      &:after {
        background-color: #F35958;
        border: 1px solid #F35958;
      }
    }
    input[type=radio]:checked {
      & + label {
        &:before {
          border: 1px solid #F35958;
        }
      }
    }
  }
  input[type=radio]:checked {
    & + label {
      color: #4d5056;
      &:after {
        -o-transform: scale(0.63);
        -ms-transform: scale(0.63);
        -moz-transform: scale(0.63);
        -webkit-transform: scale(0.63);
        transform: scale(0.63);
        line-height: 18px;
        -o-transition: -o-transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -ms-transition: -ms-transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -moz-transition: -moz-transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -webkit-transition: -webkit-transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -o-transition: transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -ms-transition: transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -moz-transition: transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        -webkit-transition: transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
        transition: transform .1s 0s cubic-bezier(0.455, 0.030, 0.215, 1.330);
      }
      &:before {
        border: 1px solid #8e9095;
      }
    }
  }
}

.checkbox {
  input[type=radio][disabled] {
    & + label {
      &:after {
        background-color: #eceff3;
      }
    }
  }
  label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    padding-left: 25px;
    margin-right: 15px;
    font-size: 13px;
    margin-bottom: 6px;
    color: #777a80;
    transition: border 0.2s linear 0s, color 0.2s linear 0s;
    margin-bottom: 10px;
    direction: ltr;
    &:before {
      content: "";
      display: inline-block;
      width: 17px;
      height: 17px;
      margin-right: 10px;
      position: absolute;
      left: 0px;
      top: 1.4px;
      background-color: $color-white;
      border: 1px solid #c2c6cb;
      border-radius: 3px;
      transition: border 0.2s linear 0s, color 0.2s linear 0s;
    }
    &::after {
      display: inline-block;
      width: 16px;
      height: 16px;
      position: absolute;
      left: 3.2px;
      top: 0px;
      font-size: 11px;
      transition: border 0.2s linear 0s, color 0.2s linear 0s;
    }
    &:after {
      border-radius: 3px;
    }
  }
  input[type=checkbox] {
    display: none;
  }
  &.checkbox-circle {
    label {
      &:after {
        border-radius: 99px;
      }
      &:before {
        border-radius: 99px;
      }
    }
  }
  input[type=checkbox]:checked {
    & + label {
      color: #4d5056;
      &::after {
        font-family: 'FontAwesome';
        content: "\F00C";
      }
    }
  }
  input[type=checkbox][disabled] {
    & + label {
      opacity: 0.65;
      &:before {
        background-color: #eceff3;
      }
    }
  }
  &.check-success {
    input[type=checkbox]:checked {
      & + label {
        &:before {
          background-color: $color-success;
          border: 1px solid $color-success;
        }
        &::after {
          color: $color-white;
        }
      }
    }
  }
  &.check-primary {
    input[type=checkbox]:checked {
      & + label {
        &:before {
          background-color: $color-primary;
          border: 1px solid $color-primary;
        }
        &::after {
          color: $color-white;
        }
      }
    }
  }
  &.check-info {
    input[type=checkbox]:checked {
      & + label {
        &:before {
          background-color: $color-info;
          border: 1px solid $color-info;
        }
        &::after {
          color: $color-white;
        }
      }
    }
  }
  &.check-warning {
    input[type=checkbox]:checked {
      & + label {
        &:before {
          background-color: $color-warning;
          border: 1px solid $color-warning;
        }
        &::after {
          color: $color-white;
        }
      }
    }
  }
  &.check-danger {
    input[type=checkbox]:checked {
      & + label {
        &:before {
          background-color: $color-danger;
          border: 1px solid $color-danger;
        }
        &::after {
          color: $color-white;
        }
      }
    }
  }
}

table {
  td {
    .checkbox {
      label {
        padding-left: 0px;
      }
    }
  }
  th {
    .checkbox {
      label {
        padding-left: 0px;
      }
    }
  }
  &.fc-border-separate {
    margin-top: 20px;
  }
}

.star {
  label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    font-size: 18px;
    transition: border 0.2s linear 0s, color 0.2s linear 0s;
    margin-bottom: 10px;
    &:before {
      font-family: 'FontAwesome';
      content: "\F005";
      display: inline-block;
      margin-right: 10px;
      position: absolute;
      left: 0px;
      top: -4px;
      font-size: 18px;
      color: #97a3b3;
      transition: border 0.2s linear 0s, color 0.2s linear 0s;
    }
  }
  input[type=checkbox] {
    display: none;
  }
  input[type=checkbox]:checked {
    & + label {
      &:before {
        color: #ffd200;
      }
    }
  }
}

.wysihtml5-toolbar {
  li {
    padding-left: 0px;
  }
}

.bootstrap-tagsinput {
  border: 1px solid #E5E9EC;
  border-radius: 3px;
  box-shadow: none;
  width: 100%;
  transition: background 0.2s linear 0s;
  &.active-element {
    background-color: #f4f5f7;
  }
  input {
    border: none;
    margin-bottom: 0px;
    min-height: 25px;
  }
  .tag {
    vertical-align: middle;
    padding: 4px 9px;
    margin-top: 3px;
    display: inline-block;
  }
}

.bootstrap-tagsinput .tag [data-role="remove"]:hover:active, .bootstrap-tagsinput .tag [data-role="remove"]:hover {
  box-shadow: none;
}

form {
  margin: 0;
  legend {
    margin: 15px 0px 10px 0px !important;
  }
}

.column-seperation {
  & > div[class*="col-"] {
    border-right: 1px solid #ddd;
  }
  & > div[class*="col-"]:last-child {
    border-right: 0px;
  }
}

.form-no-horizontal-spacing {
  .row-fluid {
    [class*="span"] {
    }
    .side-label {
      border-right: 1px solid #dddddd;
    }
  }
  .error {
