  }
}

input.placeholder,
textarea.placeholder {
  color: #aaa !important;
}

.help-block {
  margin-top: 10px;
}

.form-inline {
  input {
    margin-bottom: 0px !important;
  }
}

.form-label {
  margin-top: 2px;
  .required {
    color: #e02222;
    font-size: 12px;
    padding-left: 2px;
  }
}

.help-inline {
  margin-top: 6px;
}

.help-small {
  font-size: 12px;
  padding-top: 0;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 0;
}

.success {
  .help-inline {
    &.ok {
      &:before {
        content: "\f00c";
        font-size: 16px;
        font-family: FontAwesome;
        font-weight: normal;
        font-style: normal;
