    margin-top: 8px;
  }
}

.form-view {
  .control-group {
    margin-top: 0px;
    margin-bottom: 5px;
  }
}

.uploader {
  margin-top: 2px !important;
}

.hint {
  padding: 5px 0px 0px 10px;
}

.form {
  form {
    margin: 0px !important;
    padding: 0px !important;
  }
  .form-actions {
    background-color: $base-light-five !important;
    margin-top: 0px !important;
    margin-top: 40px;
    padding-left: 190px;
  }
}

.form-actions {
  background-color: $base-light-five;
  border: 0px;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 19px 20px 20px;
  &:after {
    clear: both;
  }
}

.form-actions:before, .form-actions:after {
  content: "";
  display: table;
  line-height: 0;
}

.form-horizontal {
  display: inline-block;
  margin-right: 10px;
  position: relative;
  top: -4px;
  &.form-bordered {
    .control-group {
      margin: 0;
      padding: 0;
      border-bottom: 1px solid #eee;
      &:first-child {
        border-bottom: 1px solid #eee !important;
      }
      &.last {
        border-bottom: 0;
      }
      .controls {
        padding: 10px;
        position: relative;
        border-left: 1px solid #eee;
        .chosen-container {
          min-height: 32px;
          .help-block {
            clear: both;
            padding-top: 10px !important;
          }
        }
      }
      .control-label {
        margin-top: 10px;
      }
    }
    &.form-row-stripped {
      .control-group {
        &:nth-child(even) {
          background-color: #fcfcfc;
        }
      }
      .m-wrap {
        background: $color-white !important;
      }
    }
    &.form-label-stripped {
      .control-group {
        &:nth-child(even) {
          background-color: #fcfcfc;
          .controls {
            background-color: $color-white;
          }
        }
      }
    }
  }
}

.form-horizontal.form-bordered .help-inline,
.form-horizontal.form-bordered .help-block {
  padding-left: 0;
}

.grid.box .form .form-actions,
.grid.solid .form .form-actions {
  margin-left: -10px !important;
  margin-right: -10px !important;
  margin-bottom: -10px !important;
}

.wizard-steps {
  display: block;
  list-style: none outside none;
  padding: 0;
  position: relative;
  width: 100%;
  li {
    display: block;
    float: left;
    max-width: 25%;
    min-width: 25%;
    text-align: center;
    padding-left: 0;
    &:before {
      border-top: 6px solid #55606E;
      content: "";
      display: block;
      font-size: 0;
      overflow: hidden;
      position: relative;
      top: 13px;
      right: 1px;
      width: 100%;
      z-index: 1;
    }
    &:first-child {
      &:before {
        left: 50%;
        max-width: 50%;
      }
    }
    &:last-child {
      &:before {
        max-width: 50%;
        width: 50%;
      }
    }
    &.complete {
      .step {
        background-color: $color-primary;
        padding: 1px 6px;
        border: 4px solid #55606e;
      }
    }
    .step {
      background-color: $color-primary-grey;
      border-radius: 32px 32px 32px 32px;
      color: $color-white;
      display: inline;
      font-size: 15px;
      font-weight: bold;
      line-height: 12px;
      padding: 4px 9px;
      position: relative;
      text-align: center;
      z-index: 2;
      transition: all 0.2s linear 0s;
      i {
        font-size: 10px;
        font-weight: normal;
        position: relative;
        top: -1.5px;
      }
    }
    .title {
      color: #B1BCC5;
      display: block;
      font-size: 13px;
      line-height: 15px;
      max-width: 100%;
      position: relative;
      table-layout: fixed;
      text-align: center;
      top: 20px;
      word-wrap: break-word;
      z-index: 104;
    }
  }
}

.wizard-steps a:hover, .wizard-steps a:active, .wizard-steps a:focus {
  text-decoration: none;
}

.wizard-steps li.active .step, .wizard-steps li.active.complete .step {
  background-color: $color-success;
  color: $color-white;
  font-weight: bold;
  padding: 10px 15px;
  border: none;
  font-size: 16px;
}

.wizard-steps li.complete .title, .wizard-steps li.active .title {
  color: #2B3D53;
}

.step-content {
  margin-left: 60px;
  margin-top: 40px;
  .step-pane {
    display: none;
    min-height: 267px;
  }
  .active {
    display: block;
  }
}

.wizard-actions {
  display: block;
  list-style: none outside none;
  padding: 0;
  position: relative;
  width: 100%;
  li {
    display: inline;
  }
}

.tab-content {
  overflow: auto;
  position: relative;
  z-index: 10;
  background-color: $color-white;
  overflow: hidden;
  margin-bottom: 18px;
  &.transparent {
    background-color: transparent;
  }
}

.form-group {
  margin-bottom: 20px;
  .form-label {
    color: #55606e;
    display: inline-block;
    margin-right: 8px;
  }
  .help {
    color: #A7ABB1;
    font-size: 12px;
  }
  .controls {
    margin-top: 8px;
  }
}

.form-control {
  border: 1px solid #E5E9EC;
  font-size: 13px;
}

.form-row input, .form-row select, .form-row textarea, .form-row select2 {
  margin-bottom: 10px;
}

.form-row {
  .radio {
    margin-bottom: 7px;
  }
